	import tTable from '@/components/t-table/t-table.vue'
	import tTh from '@/components/t-table/t-th.vue'
	import tTr from '@/components/t-table/t-tr.vue'
	import tTd from '@/components/t-table/t-td.vue'
	export default {
		components: {
		    tTable,
		    tTh,
		    tTr,
		    tTd
		},
		data() {
			return {
				msg:'',
				uid:"",
				curr_worktype_name:'',
				curr_worktype_bh:'',
				chuanghao:'',
				huohao:'',
				worktype:"",
				sl:'',
				price:'',
				je:'',
				info:'',
				list:[],
				userid:'',
				color:'',
				chima:'',
				ganghao:'',
				dk:''
			}
		},
		onBackPress(options) {  
			console.log(JSON.stringify(options));
			if (options.from === 'backbutton') {
				uni.switchTab({
					url: '/pages/index/index'
				});
				return true; 
            }  
            return false;  
        }, 
		onLoad(r) {
			this.userid = r.result;   //条码
			this.curr_worktype_bh = r.bh;   //工序号
			//console.log(r);
			this.dk = this.websiteUrl,
			uni.getStorage({
				key:'fwqsj',success: res => {
					this.dk = res.data
				}
			})
			uni.getStorage({
				key:'loginData',success: res =>{
					this.uid = res.data.uid
				}			
			})
			
		    uni.request({ 
				url:`https://${this.dk}/downloadDeviceCMDs.aspx`,
				method:'GET',
				data:{
					uid: this.uid,
					worktype: this.curr_worktype_bh,
					Info: this.userid,
					Inputsl:0
				},
				complete: res => {
					if(res.statusCode == 200){
						this.huohao = res.data.huohao;
						this.color = res.data.color;
						this.chima = res.data.chima;
						this.chuanghao = res.data.chuanghao;
						this.sl = res.data.sl;
						this.msg = res.data.tsxx;
						
						uni.request({
							url: `https://${this.dk}/getSearchWorktype.aspx`,
							method: 'GET',
							data: {
								userid:this.userid
							},
							success: res => {
								if(res.statusCode == 200){
									//console.log(JSON.stringify(res));
									this.list = res.data;
								}
							}
						})
						
					}else{
						this.list = [];
						this.huohao = '';
						this.color = '';
						this.chima = '';
						this.chuanghao = '';
						uni.showToast({
							 position:'top',
							 title:'网络异常，请重试',
							 icon :'none',
							 duration: 3000
						})
					}
				}
			}) 
		},
		methods: {
			onpeBtn(){
				uni.scanCode({
					onlyFromCamera: true,
					scanType:['qrCode','barCode'],   //这里有个坑，不写的话扫描速度有点慢
					success: res => {
						this.userid = res.result;
						//第一部分数据
						uni.request({
							url:`https://${this.dk}/downloadDeviceCMDs.aspx`,
							method:'GET',
							data:{
								uid: this.uid,
								worktype: this.curr_worktype_bh,
								Info: res.result,
								Inputsl:0
							},
							complete: res => {	
								if(res.statusCode == 200){
									this.huohao = '';
									this.color = '';
									this.chima = '';
									this.ganghao = '';
								    this.huohao = res.data.huohao;
									this.color =  res.data.color;
									this.chima = res.data.chima;
									this.chuanghao = res.data.chuanghao;
									
									this.msg = res.data.tsxx,
									
									uni.request({
										url: `https://${this.dk}/getSearchWorktype.aspx`,
										method: 'GET',
										data: {
											userid:this.userid
										},
										success: res => {
											if(res.statusCode == 200){
												//console.log(JSON.stringify(res));
												this.list = res.data;
											}
										}
									})
									
								}else{
									this.list = [];
									this.huohao = '';
									this.color = '';
									this.chima = '';
									this.ganghao = '';
									uni.showToast({
										 position:'top',
										 title:'请扫描正确的条码',
										 icon :'none',
										 duration: 2000
									})
								}
							}
						})
					}
				})
			}
		}
	}
